import React, { Component } from "react";
import { GridList, Card, CardTitle, CardText, List, ListItem, Subheader, FontIcon } from "react-md";
import { MdAttachMoney, MdLooksOne, MdLooksTwo } from "react-icons/lib/md";

export default class Regra extends Component {

  render() {
    const regras = [
      {
        titulo: "Três Pontos",
        subTitulo: "3 (três)",
        descricao: "Os boleiros que acertarem o placar do jogo receberão 3 (três) pontos. Ou seja, a quantidade de gols do mandante e visitante são iguais ao do gabarito respectivamente."
      },
      {
        titulo: "Um Ponto",
        subTitulo: "1 (um)",
        descricao: "Os boleiros que errarem o placar, porém que acertarem o resultado do jogo, mesmo em caso de empate, receberão 1 (um) ponto. Ou seja, a quantidade de gols do mandante e visitante são proporcionais ao do gabarito respectivamente."
      },
      {
        titulo: "Critério de Desempate",
        subTitulo: "Número de Placares",
        descricao: "Caso exitam dois ou mais boleiros com o mesmo número de pontos, terão preferência aqueles que possuírem o maior número de placares."
      },
      {
        titulo: "Ver Palpites dos outros",
        subTitulo: "Depois do início da Copa",
        descricao: "Somente será possível ver os palpites dos outros boleiros a partir do início da copa."
      },
      {
        titulo: "Registrar Palpites",
        subTitulo: "1 (um) dia antes do início da copa",
        descricao: "O registro de palpites estarão liberados até 1 (um) dia antes do início da copa."
      },
      {
        titulo: "Valor do Bolão",
        subTitulo: "R$ 30,00",
        descricao: "Cada participante contribui com R$ 30,00."
      },
      {
        titulo: "Divisão do Prêmio",
        subTitulo: "1º, 2º",
        descricao: [
          {
            regra: "Total Arrecadado: TA",
            valor: "Boleiros * R$ 30,00",
            icone: <MdAttachMoney />
          },
          {
            regra: "2º Lugar: 2L",
            valor: "R$ 30,00",
            icone: <MdLooksTwo />
          },
          {
            regra: "1º Lugar: 1L",
            valor: "TA - 2L",
            icone: <MdLooksOne />
          }
        ]
      }
    ];
    return (
      <GridList container="pictures" size={12} component="section">
        {
          regras.map(({ titulo, subTitulo, descricao }) => (
            <Card key={titulo}>
              <CardTitle title={titulo} subtitle={subTitulo} />
              {
                Array.isArray(descricao)
                  ? <List>
                    {
                      descricao.map(({ regra, valor, icone }, i) => regra === "Cabecalho"
                        ? <Subheader key={i} style={{ "backgroundColor": "#ffc400" }} primary primaryText={valor} />
                        : <ListItem
                          key={i}
                          leftIcon={<FontIcon primary>{icone}</FontIcon>}
                          primaryText={regra}
                          secondaryText={valor}>
                        </ListItem>
                      )
                    }
                  </List>
                  :
                  <CardText>
                    <p>{descricao}</p>
                  </CardText>
              }
            </Card>
          ))
        }
      </GridList>
    );
  }
}
